.container { 
    margin-left: auto;
    margin-right: auto;

    width: 100%;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: $mobile) {
        width: $mobile;
        
    }
    
    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding-left: 75px;
        padding-right: 75px;
    }
    @media screen and (min-width: $desktop) {
        width: $desktop;
        padding-left: 71px;
        padding-right: 71px;
        
    }

}